img {
  max-width: 100%; }

h1, h2, h3, p {
  text-align: center;
  margin: 0; }

body {
  background-color: #090b1a; }

.container {
  max-width: 30rem;
  margin: 2rem auto;
  display: flex;
  justify-content: center;
  align-items: center; }
  .container .card {
    display: grid;
    grid-template-rows: 1fr 2fr; }
    .container .card .info {
      display: flex;
      flex-direction: column;
      align-items: center;
      background-color: #1b1938;
      padding: 3rem 2rem 1rem 2rem;
      border-radius: 0rem 0rem 1rem 1rem; }
      .container .card .info .title {
        font-size: 3.4rem;
        font-family: "Inter", sans-serif;
        font-weight: 700;
        color: white;
        margin-bottom: 2rem; }
        .container .card .info .title span {
          color: #aa5cdb; }
      .container .card .info .parrafo {
        width: 80%;
        font-size: 1.4rem;
        font-family: "Inter", sans-serif;
        font-weight: 400;
        line-height: 1.8;
        color: rgba(255, 255, 255, 0.75);
        margin-bottom: 4.5rem; }
    .container .card .container-img {
      position: relative;
      grid-row: 1/2; }
      .container .card .container-img .overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgba(100, 10, 190, 0.4);
        border-radius: 1rem 1rem 0rem 0rem; }
      .container .card .container-img .img {
        width: 34rem;
        height: 100%;
        background-image: url(../../../src/img/image-header-mobile.jpg);
        background-size: cover;
        border-radius: 1rem 1rem 0rem 0rem; }

.stats .stat {
  margin-bottom: 3.5rem; }

.stats .cantidad {
  font-size: 2rem;
  color: white;
  font-family: "Inter", sans-serif;
  font-weight: 700; }

.stats .tipo {
  font-size: 1.1rem;
  color: rgba(255, 255, 255, 0.6);
  font-family: "Lexend Deca", sans-serif;
  text-transform: uppercase; }

@media (min-width: 1440px) {
  .container {
    height: 100vh;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center; }
    .container .card {
      display: grid;
      grid-template-rows: 1fr;
      grid-template-columns: 1fr 1fr; }
      .container .card .info {
        display: block;
        padding: 7rem 9rem 7rem 7rem;
        width: 52rem;
        border-radius: 1rem 0 0 1rem; }
        .container .card .info h1, .container .card .info p {
          text-align: left;
          margin: 0; }
        .container .card .info .title {
          margin-top: 0;
          margin-bottom: 3rem; }
        .container .card .info .parrafo {
          width: 90%;
          margin-bottom: 7rem; }
      .container .card .container-img {
        grid-column: 2/3; }
        .container .card .container-img .overlay {
          border-radius: 0rem 1rem 1rem 0rem; }
        .container .card .container-img .img {
          width: 52rem;
          height: 44rem;
          background-image: url(../../../src/img/image-header-desktop.jpg);
          border-radius: 0rem 1rem 1rem 0rem; }
  .stats {
    display: grid;
    grid-template-columns: repeat(3, 1fr); }
    .stats .stat {
      margin-bottom: 0;
      margin-right: 7rem; }
    .stats .cantidad {
      margin-bottom: .5rem; } }
